.Modal {
    position: fixed;
    z-index: 500;
    background-color: white;
    width: 70%;
    height: 70%;
    padding: 16px 0px 0px 0px;
    left: 15%;
    top: 15%;
    box-sizing: border-box;
    transition: all 0.3s ease-out;
    border-radius: 8px;
}

@media (min-width: 600px) {
    .Modal {
        width: 500px;
        left: calc(50% - 250px);
    }
}